* {
  padding: 0;
  margin: 0;
}

.wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}

.animated-check {
  height: 5em;
  width: 10em;
  animation-name: swell;
  animation-duration: 0.8s;
}
@keyframes swell {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  100% {
    transform: scale(8);
    opacity: 0;
  }
}

.animated-check path {
  fill: none;
  stroke: #88da49;
  stroke-width: 4;
  stroke-dasharray: 23;
  stroke-dashoffset: 23;
  animation: draw 0.3s linear forwards;
  stroke-linecap: round;
  stroke-linejoin: round;
}

@keyframes draw {
  to {
    stroke-dashoffset: 0;
  }
}
